@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@import url('../components/shared/tokens.css');
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Reusable layout classes */
@layer components {
  .hl-page {
    @apply flex flex-col h-screen bg-pk-bg text-pk-text font-pk;
  }
  .hl-scroll {
    @apply flex-1 overflow-y-auto px-3 pb-24 scroll-smooth lg:px-4 lg:pb-8 lg:max-w-[1200px] lg:mx-auto;
  }
  .hl-trading {
    @apply flex-1 overflow-hidden px-3 pb-3 lg:px-4 lg:pb-4;
  }
  .hl-topbar {
    @apply w-full flex items-center justify-between gap-4 rounded-xl border shadow-sm px-4 py-3;
  }
}

/* Global reset and base styles */
* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: var(--pk-font-family);
  font-size: var(--pk-font-size-body);
  font-weight: var(--pk-font-weight-body);
  line-height: var(--pk-line-height-body);
  background: var(--pk-color-bg);
  color: var(--pk-color-text);
}

/* Typography utilities */
.pk-h1 {
  font-size: var(--pk-font-size-h1);
  font-weight: var(--pk-font-weight-h1);
  line-height: var(--pk-line-height-h1);
  margin: 0;
}

.pk-h2 {
  font-size: var(--pk-font-size-h2);
  font-weight: var(--pk-font-weight-h2);
  line-height: var(--pk-line-height-h2);
  letter-spacing: 0.01em;
  margin: 0;
}

.pk-body {
  font-size: var(--pk-font-size-body);
  font-weight: var(--pk-font-weight-body);
  line-height: var(--pk-line-height-body);
  margin: 0;
}

.pk-caption {
  font-size: var(--pk-font-size-caption);
  font-weight: var(--pk-font-weight-caption);
  line-height: var(--pk-line-height-caption);
  color: var(--pk-color-text-secondary);
  margin: 0;
}

/* Global animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes flash {
  0% {
    background-color: transparent;
  }
  50% {
    background-color: var(--pk-color-accent);
  }
  100% {
    background-color: transparent;
  }
}

/* Utility classes */
.animate-fade-in {
  animation: fadeIn 0.3s ease-out;
}

.animate-pulse {
  animation: pulse 2s ease-in-out infinite;
}

.animate-flash {
  animation: flash 0.6s ease-in-out;
}

/* Base widget styles */
.pk-widget {
  all: initial;
  font-family: var(--pk-font-family);
  box-sizing: border-box;
}

.pk-widget *,
.pk-widget *::before,
.pk-widget *::after {
  box-sizing: border-box;
}

/* Animation keyframes */
@keyframes flash-green {
  0% {
    background-color: rgb(16 185 129);
  }
  50% {
    background-color: rgb(16 185 129 / 0.3);
  }
  100% {
    background-color: transparent;
  }
}

@keyframes flash-red {
  0% {
    background-color: rgb(239 68 68);
  }
  50% {
    background-color: rgb(239 68 68 / 0.3);
  }
  100% {
    background-color: transparent;
  }
}

@keyframes pulse-success {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
}

/* Custom scrollbar for order book */
.pk-widget ::-webkit-scrollbar {
  width: 6px;
}

.pk-widget ::-webkit-scrollbar-track {
  background: var(--pk-color-surface);
}

.pk-widget ::-webkit-scrollbar-thumb {
  background: var(--pk-color-text-secondary);
  border-radius: 3px;
  opacity: 0.3;
}

.pk-widget ::-webkit-scrollbar-thumb:hover {
  background: var(--pk-color-text-secondary);
  opacity: 0.6;
}
