:root {
  /* Colors - Dark Theme Defaults */
  --pk-color-bg: #121316;
  --pk-color-surface: #1e1f24;
  --pk-color-surface-secondary: #2e2f34;
  --pk-color-surface-tertiary: #3e3f44;
  --pk-color-text: #f3f4f8;
  --pk-color-text-secondary: #9ca3af;
  --pk-color-accent: #6684ff;
  --pk-color-accent-dim: hsl(230, 100%, 71%, 0.16); /* 16% tint of accent */
  --pk-color-error: #ff5860;
  --pk-color-green: #10b981;

  /* Spacing - 4pt baseline grid */
  --pk-space-1: 4px;
  --pk-space-2: 8px;
  --pk-space-3: 12px;
  --pk-space-4: 16px;

  /* Border radius */
  --pk-radius: 8px;

  /* Typography - Inter/System font stack */
  --pk-font-family:
    Inter, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;

  /* Type scale */
  --pk-font-size-h1: 24px;
  --pk-font-size-h2: 18px;
  --pk-font-size-body: 14px;
  --pk-font-size-caption: 12px;

  --pk-font-weight-h1: 600;
  --pk-font-weight-h2: 600;
  --pk-font-weight-body: 400;
  --pk-font-weight-caption: 400;

  --pk-line-height-h1: 32px;
  --pk-line-height-h2: 26px;
  --pk-line-height-body: 22px;
  --pk-line-height-caption: 18px;

  /* Shadows */
  --pk-shadow-card: 0 1px 4px rgba(0, 0, 0, 0.15);
  --pk-shadow-card-hover: 0 4px 10px rgba(0, 0, 0, 0.25);

  /* Z-index */
  --pk-z-sticky: 10;
  --pk-z-modal: 100;
  --pk-z-tooltip: 200;
}

/* Light theme override */
[data-theme='light'] {
  --pk-color-bg: #ffffff;
  --pk-color-surface: #f8f9fb;
  --pk-color-surface-tertiary: #e2e8f0;
  --pk-color-text: #202326;
  --pk-color-text-secondary: #58606b;
  --pk-color-accent: #4f70ff;
  --pk-color-accent-dim: hsl(230, 100%, 66%, 0.08); /* 8% tint of accent */
  --pk-color-error: #ff5860;
  --pk-color-green: #16a34a;

  --pk-shadow-card: 0 1px 4px rgba(0, 0, 0, 0.06);
  --pk-shadow-card-hover: 0 4px 10px rgba(0, 0, 0, 0.1);
}
